export class VueGMDSS {
  constructor () {
    this.time = new Date()
    this.times = new Date()
    this.itemid = -1 // 3秒定时器跳转
    this.erase = -1 // 清空列表页面定时器跳转
    this.req = '' // 当前屏率
    this.ch = '' // 当前通道号
    this.chText = '' // 当前通道名称
    this.lightness = 10 // 亮度调节变量
    this.print = false // 打印状态
    this.strprint = false // 打印字符串状态
    this.bumfPrint = false // 打印纸 是否打印打印纸
    this.ioc = [' 60', ' 90', '120', '240'] // ioc速度
    this.scan = ['579', '288'] // ioc 密度
    this.iocmin = ['60', '90', '120', '240'] // IOC页面映射表 ioc
    this.scanmin = ['579', '288'] // IOC 页面隐射表 scan
    this.hous = '' // 小时
    this.mise = '' // 分钟
    // this.hous = this.time.getHours() // 小时
    // this.mise = this.time.getMinutes() // 分钟
    this.recall = []
    this.timeing = [] // 定时打印数组列表
    this.paperNum = -400 // 图片初始高度
    this.strpaper = 0
    this.bumf = -200 // 送纸的纸 初始位置
    this.strtimeing = [] // 保存定时打印数组列表字符串
    // mode列表
    this.modeMsg = [
      'Manual START ？',
      'TIMER  ON   ？',
      'SLEEP  ON   ？',
      'MODE: Escape ？'
    ]
    // 查看设置列表
    this.recall = [
      'TIME  ',
      'Recall NAVTEX ？',
      'Recall Schedule？',
      'Recall CH FREQ？',
      'RCL : Escape  ？'
    ]
    // 定时打印页面
    this.prog = [
      'Set CLOCK  ？',
      'Set NAVTEX ？',
      'Set Schedule？',
      'Update CH FREQ？',
      'Erase Schedule ？',
      'PRG : Escape'
    ]
    // 原数据映射表
    /*
    ch 管道号
    chzText 管道名称
    req 频率
    */
    this.maxList = [
      {
        ch: '000',
        chText: 'JMH',
        req: '036225'
      },
      {
        ch: '001',
        chText: 'JMH',
        req: '073050'
      },
      {
        ch: '002',
        chText: 'JMH',
        req: '135790'
      },
      {
        ch: '010',
        chText: 'JMH',
        req: '036225'
      },
      {
        ch: '011',
        chText: 'JMH',
        req: '073050'
      },
      {
        ch: '012',
        chText: 'JMH',
        req: '135790'
      },
      {
        ch: '020',
        chText: 'JFC',
        req: '042740'
      },
      {
        ch: '021',
        chText: 'JFC',
        req: '064145'
      },
      {
        ch: '022',
        chText: 'JFC',
        req: '086580'
      }
    ]
  }
  doTimer () {
    let time = this.time.getTime()
    time += 1000
    this.time = new Date(time)
  }
}



