<template>
  <button class= "dial" :style="rotate"
    @mousedown="mousedown($event)" @mouseup="mouseup($event)"
    @mousemove="doMouseMove($event)" @mouseout="mouseup($event)">
  </button>
</template>

<script>
export default {
  name: 'dial',
  data () {
    return {
      start: false,
      angle: 0,
      dialx: 0,
      dialy: 0,
      x: 0,
      y: 0
    }
  },
  props: {
    dialObj: {
      type: Object,
      default: function () {
        return {
          turnPlaceNum: 10,
          turnMin: 0,
          turnMax: 0
        }
      }
    }
  },
  computed: {
    rotate: function () {
      let num = this.angle
      let rotate = `{transform: rotate(${num}deg); -ms-transform: rotate(${num}deg); -moz-transform: rotate(${num}deg); -webkit-transform: rotate(${num}deg); -o-transform: rotate(${num}deg);}`
      return rotate
    }
  },
  methods: {
    getAngle: function () {
      let angle = Math.atan2(this.y - this.dialy, this.x - this.dialx) * 180.0 / Math.PI
      return Math.floor(angle + 90)
    },
    judgeTurn: function (newAngle) {
      let oldAngle = this.angle
      if (newAngle < 0 && oldAngle > 180) {
        oldAngle = oldAngle - 360
      }
      if (oldAngle < 0 && newAngle > 180) {
        oldAngle = 360 + oldAngle
      }
      let placeAngle = 360 / this.dialObj.turnPlaceNum
      let oldPlace = parseInt(oldAngle / placeAngle)
      if (oldAngle < 0) {
        oldPlace = parseInt((360 + oldAngle) / placeAngle)
      }
      let newPlace = parseInt(newAngle / placeAngle)
      if (newAngle < 0) {
        newPlace = parseInt((360 + newAngle) / placeAngle)
      }
      if (oldPlace !== newPlace) {
        if (oldAngle < newAngle) {
          this.$emit('turn', true)
        } else {
          this.$emit('turn', false)
        }
      }
    },
    judgeLimit: function (angle) {
      if (this.dialObj.turnMin === 0) {
        let val = angle
        if (val < 0) {
          val = 360 + val
        }
        if (val > this.dialObj.turnMax) {
          if (this.dialObj.turnMax > 270) {
            angle = this.dialObj.turnMax - 360
          } else {
            angle = this.dialObj.turnMax
          }
        }
        return angle
      }

      if (angle > this.dialObj.turnMax && angle <= 180) {
        angle = this.dialObj.turnMax
      }
      if (angle < 0) {
        if (angle < this.dialObj.turnMin) {
          angle = this.dialObj.turnMin
        }
      } else {
        if (angle < (this.dialObj.turnMin + 360) && angle > 180) {
          angle = this.dialObj.turnMin + 360
        }
      }

      return angle
    },
    doMouseMove: function (event) {
      if (this.start === true) {
        this.x = event.clientX
        this.y = event.clientY
        let angle = this.getAngle()
        if (this.dialObj.turnMin !== 0 || this.dialObj.turnMax !== 0) {
          angle = this.judgeLimit(angle)
        }
        this.judgeTurn(angle)
        this.angle = angle
        this.$emit('isAngle', this.angle)
      }
    },
    mousedown: function (event) {
      var rect = event.target.getBoundingClientRect()
      this.start = true
      this.dialx = rect.left + 0.5 * rect.width
      this.dialy = rect.top + 0.5 * rect.height
    },
    mouseup: function (event) {
      this.start = false
    }
  }
}
</script>

<style>
.dial{
  border: none;
  cursor: pointer;
  outline: none;
}
</style>



