// 清除定时列表
<template>
  <div id="erase" v-router-page:page-name = "'/erase'">
    <div id="erase-noe" v-show="eraseType">
      <div>Erase  OK ?</div>
      <div class="erase-div">
      <div>(</div>
      <div :class= "index === 0 ? 'erase-bom' : undefined">Y</div>
      <div>/</div>
      <div :class= "index === 1 ? 'erase-bom' : undefined">N</div>
      <div>)</div>
      </div>
    </div>
    <div v-show="yese">Erase</div>
    <div v-show="no">Escape</div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  data () {
    return {
      gmdss: this.$frame.gmdss,
      index: 0, // 光标下标
      eraseType: true, // 显示选项
      yese: false, // 未点击Y
      no: false // 未点击N
    }
  },
  methods: {
    // 光标左切换
    indexLeft () {
      if (this.index > 0) {
        this.index -= 1
      }
    },
    // 光标右切换
    indexRight () {
      if (this.index < 1) {
        this.index += 1
      }
    },
    // 路由跳转方法 2秒跳主页面
    router () {
      let _this = this
      this.gmdss.erase = setTimeout(function () {
        _this.$frame.routeTo('/first')
      }, 2000)
    },
    // 点击ENT要做的操作
    entFn () {
      if (this.index === 0) {
        this.eraseType = false
        this.no = false
        this.yese = true
        // 清空定时打印数组列表
        this.gmdss.timeing = []
        this.router()
      }
      if (this.index === 1) {
        this.eraseType = false
        this.yese = false
        this.no = true
        this.router()
      }
    }
  },
  [EventKeys.EVT_LED] (evt) {
    console.log('led', '66666666666')
  },
  [EventKeys.EVT_RCL] (evt) {
    console.log('rcl', '66666666666')
  },
  [EventKeys.EVT_MOPE] (evt) {
    console.log('mope', '66666666666')
  },
  [EventKeys.EVT_LEFT] (evt) {
    this.indexLeft()
  },
  [EventKeys.EVT_RIGHT] (evt) {
    this.indexRight()
  },
  [EventKeys.EVT_UP] (evt) {
    console.log('up', '66666666666')
  },
  [EventKeys.EVT_DOWN] (evt) {
    console.log('down', '66666666666')
  },
  [EventKeys.EVT_TIM] (evt) {
    console.log('tim', '66666666666')
  },
  [EventKeys.EVT_PHASE] (evt) {
    console.log('phase', '66666666666')
  },
  [EventKeys.EVT_IOC] (evt) {
    console.log('ioc', '66666666666')
  },
  [EventKeys.EVT_PH] (evt) {
    console.log('ph', '66666666666')
  },
  [EventKeys.EVT_ENT] (evt) {
    this.entFn()
    console.log('ent', '66666666666')
  },
  [EventKeys.EVT_POWER] (evt) {
  }
}
</script>

<style>
#erase{
  width: 100%;
  height: 100%;
  font-size: 23px;
  text-align: center;
}
#erase-noe{
  display: flex;
  justify-content: space-around;
}
.erase-div{
  display: flex;
}
#erase > div {
height: 35px;
line-height: 35px;
}
.erase-bom{
  border-bottom: 2px solid black;
}
</style>



