// 定时打印设置
<template>
  <div id="program" v-router-page:page-name = "'/program'">
  <div class="program-div" v-for="(item,index) in this.$frame.gmdss.prog" :key="index"
    v-show= "indexs === index">{{item}}</div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  data () {
    return {
      gmdss: this.$frame.gmdss,
      indexs: 0
    }
  },
  watch: {
    // 循环选择
    indexs (ovel, nevl) {
      if (ovel < 0) {
        this.indexs = this.gmdss.prog.length - 1
      }
      if (ovel > this.gmdss.prog.length - 1) {
        this.indexs = 0
      }
    }
  },
  methods: {
    // 向上选择
    progUp () {
      this.indexs += 1
    },
    // 向下选择
    progDown () {
      this.indexs -= 1
    },
    // 进入定时设置页面
    progEnt () {
      if (this.indexs === 2) {
        this.$frame.routeTo('/timeing')
      }
      if (this.indexs === 4) {
        this.$frame.routeTo('/erase')
      }
      if (this.indexs === 5) {
        this.$frame.routeTo('/first')
      }
    }
  },
  [EventKeys.EVT_RCL] (evt) {
    console.log('rcl', '66666666666')
  },
  [EventKeys.EVT_UP] (evt) {
    console.log('up', '66666666666')
    this.progUp()
  },
  [EventKeys.EVT_DOWN] (evt) {
    console.log('down', '66666666666')
    this.progDown()
  },
  [EventKeys.EVT_ENT] (evt) {
    this.progEnt()
  },
  [EventKeys.EVT_POWER] (evt) {
  }
}
</script>

<style>
#program{
  width: 100%;
  height: 100%;
  font-size: 23px;
  text-align: center;
  line-height: 39px;
}
</style>



