<template>
  <div id="app"  v-router-page:page-name = "'/'">
    <div class="router" :style="brightness">
    <router-view/>
    </div>
    <button id="led" @mousedown="mousedown('led')" @mouseup="mouseup('led')"></button>
    <button id="rcl" @mousedown="mousedown('rcl')" @mouseup="mouseup('rcl')" @contextmenu.prevent="rclTrue()"></button>
    <button id="mope" @mousedown="mousedown('mope')" @mouseup="mouseup('mope')"></button>
    <button id="left" @mousedown="mousedown('left')" @mouseup="mouseup('left')"></button>
    <button id="right" @mousedown="mousedown('right')" @mouseup="mouseup('right')"></button>
    <button id="up" @mousedown="mousedown('up')" @mouseup="mouseup('up')"></button>
    <button id="down" @mousedown="mousedown('down')" @mouseup="mouseup('down')"></button>
    <button id="tim" @mousedown="mousedown('tim')" @mouseup="mouseup('tim')"></button>
    <button id="phase" @mousedown="mousedown('phase')" @mouseup="mouseup('phase')"></button>
    <button id="ioc" @mousedown="mousedown('ioc')" @mouseup="mouseup('ioc')"></button>
    <button id="ph" @mousedown="mousedown('ph')" @mouseup="mouseup('ph')"></button>
    <button id="ent" @mousedown="mousedown('ent')" @mouseup="mouseup('ent')"></button>
    <button id="power" @mousedown="mousedown('power')" @mouseup="mouseup('power')"></button>
    <div v-show="handType" class="hand" id="hand"></div>
    <div class="ledLat" id="led1"></div>
    <div :class="{'ledLat':printType === false, 'ledFat':printType === true}" id="led2"></div>
    <div class="ledLat" id="led3"></div>
    <div class="ledLat" id="led4"></div>
    <div class="ledLat" id="led5"></div>
    <div class="ledLat" id="led6"></div>
    <div class="ledLat" id="led7"></div>
    <div class="ledLat" id="led8"></div>
    <div class="ledLat" id="led9"></div>
    <div :class="{'ledLat':ledTyle === false, 'ledFat':ledTyle === true}" id="led10"></div>
    <div class="ledLat" id="led11"></div>
    <!-- 旋钮1 -->
    <dial class='knob' id="knob1"></dial>
    <!-- 旋钮2 -->
    <dial id="knob2" class="knob"></dial>
    <!-- 可以放大的海图 -->
    <div v-show="!paper" class="shadow"><div id="mypaper" class="bg-top" @click="paperFn" @mousewheel="doMouseWheel()"></div></div>
  <!-- 打印图片 -->
    <div class="bg-paper" v-show= "gmdss.print === true">
      <div class="bg-paper1" v-show="!this.gmdss.strprint">
        <div v-show="paper" @click="paperFn" class="bg-paper2" :style="porderTop"></div>
      </div>
    </div>
    <!-- 打印字符串 -->
    <div class="bg-paper3" v-show= "gmdss.strprint === true">
      <div class="bg-paper4" v-show="this.gmdss.strprint">
        <div class="str-print" :style="strTop">
        <div v-for="(item,index) in this.gmdss.strtimeing" :key="index">{{item}}</div>
        </div>
      </div>
    </div>
    <!-- 送纸 -->
     <div class="bg-paper5" v-show= "gmdss.bumfPrint === true">
      <div class="bg-paper6">
        <div class="str-print1" :style="bumfTop">
        </div>
      </div>
    </div>
    <!-- 电源灯 -->
    <div :class="{'powerLat':ledTyle === false, 'powerFat':ledTyle === true}"></div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
import dial from '@/components/dial'
export default {
  name: 'App',
  data () {
    return {
      gmdss: this.$frame.gmdss,
      ledTyle: false, // 开机状态为true 关机状态为 false
      handType: false,
      handitem: -1, // 手存在的时间定时器
      // timeing: false, // 是否打印定时列表
      intervalId: -1, // 打印图定时器
      strVale: -1, // 打印字符串
      bumfId: -1, // 打印纸定时器
      knobOne: false, // 旋钮1状态
      knobTwo: false, // 旋钮2状态
      printType: false, // 打印灯状态
      paper: true, // false 显示放大后的图片
      widthbit: 8
    }
  },
  watch: {
    // 打印字符串
    timeing (ovel, nevl) {
      if (ovel === true) {
        this.strporderFn()
        this.gmdss.print = false
        this.gmdss.bumfPrint = false
      } else {
        this.clearStr()
      }
    },
    // 监听电源状态
    ledTyle (ovel, nevl) {
      if (ovel === true) {
        this.$frame.routeTo('/itemled')
      } else {
        // 清空了3秒定时器跳转
        clearTimeout(this.gmdss.itemid)
        this.$frame.routeTo('/')
      }
    },
    // 监听亮度值 如果达到超过最高则变回最低
    ligthNum (ovel, nevl) {
      if (ovel > 25) {
        this.gmdss.lightness = 10
      }
    },
    // 监听是否打印
    print (ovel, nevl) {
      if (ovel === true) {
        this.porderFn()
        this.gmdss.strprint = false
        this.gmdss.bumfPrint = false
      } else {
        this.clearPorder()
        this.gmdss.modeMsg.splice(0, 1, 'Manual TART ？')
        this.printType = false
      }
    },
    // 是否打印打印纸
    bumfPrint (ovel, nevl) {
      if (ovel === true) {
        this.bumfFn()
        this.gmdss.strprint = false
        this.gmdss.print = false
      } else {
        this.clerBumf()
      }
    }
  },
  computed: {
    // 亮度调节
    brightness () {
      let num = parseInt(this.gmdss.lightness / 2) + 6
      let value = num / 10
      let s = `{filter: brightness(${value}); -webkit-filter: brightness(${value});}`
      return s
    },
    // 返回现在亮度值
    ligthNum () {
      return this.gmdss.lightness
    },
    // 图片样式
    porderTop () {
      let num = this.paperNum
      let s = {top: `${num}px`, '-webkit-top': `${num}px`}
      return s
    },
    // 字符样式
    strTop () {
      let num = this.strNum
      let s = {top: `${num}px`, '-webkit-top': `${num}px`}
      return s
    },
    // 打印纸样式
    bumfTop () {
      let num = this.bumfNum
      let s = {top: `${num}px`, '-webkit-top': `${num}px`}
      return s
    },
    // 图片top值
    paperNum () {
      return this.$frame.gmdss.paperNum
    },
    // print 是否打印
    print () {
      return this.$frame.gmdss.print
    },
    // 字符串top值
    strNum () {
      return this.gmdss.strpaper
    },
    // 是否打印字符串
    timeing () {
      return this.gmdss.strprint
    },
    // 打印打印纸TOP值
    bumfNum () {
      return this.gmdss.bumf
    },
    // 是否打印打印纸
    bumfPrint () {
      return this.gmdss.bumfPrint
    }
  },
  methods: {
    // 小手显示
    rclTrue () {
      if (this.ledTyle === false) {
        let _this = this
        this.handType = true
        this.handitem = setTimeout(function () {
          _this.handType = false
        }, 5000)
      }
    },
    // 电源事件
    powerFn () {
      this.ledTyle = !this.ledTyle
      if (this.handType === true) {
        this.gmdss.strprint = true
      }
      if (this.ledTyle === false) {
        this.gmdss.strprint = false
        this.gmdss.print = false
        this.gmdss.bumfPrint = false
      }
    },
    // 消除
    rclFalse () {
      clearTimeout(this.handitem)
      this.handType = false
    },
    // 屏幕亮度调节
    lightness () {
      if (this.ledTyle === true) {
        this.gmdss.lightness += 5
      }
    },
    // 打印传真
    porderFn () {
      let _this = this
      _this.intervalId = setInterval(function () {
        if (_this.$frame.gmdss.paperNum < 0) {
          _this.printType = true
          _this.$frame.gmdss.paperNum += 10
        }
        if (_this.$frame.gmdss.paperNum >= 0) {
          _this.printType = false
          clearInterval(_this.intervalId)
        }
      }, 500)
    },
    // 打印字符串
    strporderFn () {
      let _this = this
      _this.strVale = setInterval(function () {
        if (_this.$frame.gmdss.strpaper < 0) {
          _this.printType = true
          _this.$frame.gmdss.strpaper += 10
        }
        if (_this.$frame.gmdss.strpaper >= 0) {
          _this.printType = false
          clearInterval(this.strVale)
        }
      }, 500)
    },
    // 打印打印纸
    bumfFn () {
      let _this = this
      _this.bumfId = setInterval(function () {
        if (_this.gmdss.bumf < 0) {
          _this.printType = true
          _this.gmdss.bumf += 10
        }
        if (_this.gmdss.bumf >= 0) {
          _this.printType = false
          clearInterval(_this.bumfId)
        }
      }, 500)
    },
    // 停止打印打印纸
    clerBumf () {
      clearInterval(this.bumfId)
      this.gmdss.bumf = -200
    },
    // 停止打印图片
    clearPorder () {
      clearInterval(this.intervalId)
      this.gmdss.paperNum = -400
    },
    // 停止打印字符串
    clearStr () {
      clearInterval(this.strVale)
      this.$frame.gmdss.strpaper = this.gmdss.strtimeing.length * -18
      this.gmdss.strprint = false
    },
    mousedown (keyname) {
      if (this.holdonTimeout >= 0) {
        clearTimeout(this.holdonTimeout)
        this.holdonTimeout = -1
      }

      let _this = this
      // 500毫秒算长按
      this.holdonTimeout = setTimeout(function () {
        _this.btnhold(keyname)
        _this.holdonTimeout = -1
      }, 3000)
    },
    // 按钮抬起
    mouseup (keyname) {
      // 在按住阶段mouseup，当作正常点击事件
      if (this.holdonTimeout >= 0) {
        clearTimeout(this.holdonTimeout)
        this.btnclick(keyname)
      }
      this.holdonTimeout = -1
    },
    // 触发长按效果sss
    btnhold: function (keyname) {
      let fnname = EventKeys.EVT_MAP[keyname]
      let name = fnname + 'hold'
      let evt = {
        app: this,
        anykey: true,
        continue: true,
        currentpath: this.$frame.getCurrentPath(),
        holdon: true,
        keyname: name
      }
      this.btnPreprocess(evt)
      this.$frame.click(evt)
    },
    // 触发短按效果
    btnclick: function (keyname) {
      let fnname = EventKeys.EVT_MAP[keyname]
      let evt = {
        app: this,
        anykey: true,
        continue: true,
        currentpath: this.$frame.getCurrentPath(),
        holdon: true,
        keyname: fnname
      }

      this.btnPreprocess(evt)
      this.$frame.click(evt)
    },
    btnPreprocess (evt) {
      // btn 预处理 目的是有俩
      // 1、 拦截事件,  让App.vue 先处理 按钮事件， 然后在让对应page处理事件
      // 2、 生成一个 any 按钮事件
      let keyname = evt.keyname
      let fn = this.$options[keyname]
      let currentpath = this.$frame.getCurrentPath()
      if (fn === undefined) {
        console.warn('App.Vue 未定义函数[' + fn + ']()')
      } else if (currentpath !== '/') {
        fn.call(this)
      }

      let tmpkey = evt.keyname
      evt.keyname = 'any'
      this.$frame.click(evt)
      evt.keyname = tmpkey
    },
    // 传真放大
    paperFn () {
      if (this.paperNum >= 0) {
        this.paper = !this.paper
      }
    },
    // 鼠标滚轮事件 放大和缩小打印图片
    doMouseWheel (event) {
      event = event || window.event
      // event.wheelDelta 下滚为120，上滚为-120
      // event.originalEvent.wheelDelta()
      if (event.wheelDelta > 0) {
        if (this.widthbit < 10) {
          this.widthbit++
        }
      } else {
        if (this.widthbit > 2) {
          this.widthbit--
        }
      }
      // 滚轮放大缩小, 重新计算传真图片
      console.log(document.getElementById('mypaper'))
      let w = 74.5 * this.widthbit
      let h = 64 * this.widthbit
      let top = (700 - h) / 2
      let left = (950 - w) / 2
      document.getElementById('mypaper').style.top = top + 'px'
      document.getElementById('mypaper').style.top = top + 'px'
      document.getElementById('mypaper').style.left = left + 'px'
      document.getElementById('mypaper').style.width = w + 'px'
      document.getElementById('mypaper').style.height = h + 'px'
      document.getElementById('mypaper').style.backgroundSize = w + 'px' + ' ' + h + 'px'
      event.returnValue = false // 禁止浏览器滚动
    }
  },
  components: {
    dial
  },
  [EventKeys.EVT_LED] (evt) {
    this.lightness()
    this.rclFalse()
    console.log('led', '66666666666')
  },
  [EventKeys.EVT_RCL] (evt) {
    console.log('rcl', '66666666666')
  },
  [EventKeys.EVT_MOPE] (evt) {
    this.rclFalse()
    console.log('mope', '66666666666')
  },
  [EventKeys.EVT_LEFT] (evt) {
    this.rclFalse()
    console.log('left', '66666666666')
  },
  [EventKeys.EVT_RIGHT] (evt) {
    this.rclFalse()
    console.log('right', '66666666666')
  },
  [EventKeys.EVT_UP] (evt) {
    this.rclFalse()
    console.log('up', '66666666666')
  },
  [EventKeys.EVT_DOWN] (evt) {
    this.rclFalse()
    console.log('down', '66666666666')
  },
  [EventKeys.EVT_TIM] (evt) {
    if (this.ledTyle === true) {
      this.gmdss.bumfPrint = true
    }
    this.rclFalse()
    console.log('tim', '66666666666')
  },
  [EventKeys.EVT_PHASE] (evt) {
    this.rclFalse()
    console.log('phase', '66666666666')
  },
  [EventKeys.EVT_IOC] (evt) {
    this.rclFalse()
    if (this.ledTyle === true) {
      this.$frame.routeTo('/ioc')
    }
    console.log('ioc', '66666666666')
  },
  [EventKeys.EVT_PH] (evt) {
    this.rclFalse()
    console.log('ph', '66666666666')
  },
  [EventKeys.EVT_ENT] (evt) {
    this.rclFalse()
    console.log('ent', '66666666666')
  },
  [EventKeys.EVT_POWER] (evt) {
    this.powerFn()
  }
}
</script>

