export class VueEvent {
  constructor () {
    this.name = 'event'

    //  当前页面
    this._currentPage = ''

    //  page ---> vm 映射
    this._vms = {}

    //  当前事件处理的 控件对象
    //  {
    //     name：
    //     vm:
    //  }
    this._currentStack = {}

    //  控件对象堆栈
    // [{
    //   name:
    //   vm:
    // }]
    this._vmsStack = []

    // 点击事件回调函数
    this._callClickFn = undefined
    // 点击事件msg
    this.clickMsg = null
  }
  /*
  * 点击事件回调
  * path: 路由或组件路径
  * keyName: 按钮名称
  * msg：按钮事件改变的数据，根据需求传值，不需要时为null
  */
  callClickFn (path, keyName, msg) {
    let dev = window.$frame.gmdss.deviceName
    console.debug(path, keyName, msg)
    if (this._callClickFn !== undefined) {
      this._callClickFn(dev, path, keyName, msg)
    }
  }

  // 根据 pagename 获取 对应 vm
  getVm (pagename) {
    if (pagename[0] !== '/') {
      pagename = '/' + pagename
    }
    return this._vms[pagename]
  }

  addVm (pagename, vm) {
    this._vms[pagename] = vm
    console.info('addvm {page: ' + pagename + '} --- file:event.js ')
  }

  removeVm (pagename) {
    delete this._vms[pagename]
  }

  //  当事件要从当前工作区域转到
  // 下一个工作区域 需要手动调用这个
  pushStack (pagename, pagevm) {
    let that = this
    let stack = {
      name: pagename,
      vm: pagevm
    }
    that._currentStack = stack
    that._vmsStack.push(stack)
  }

  popStack () {
    this._vmsStack.pop()

    let len = this._vmsStack.length
    let stack = this._vmsStack[len - 1]

    this._currentStack = stack
  }

  getCurrentVm () {
    let len = this._vmsStack.length
    let vm = this._vmsStack[len - 1]
    return vm
  }

  //  page = ''   page 页面 地址
  //  params = {} 预留参数
  routeTo (page, params) {
    let p = page
    if (page[0] === '/') {
      let len = page.length
      page = page.substr(1, len - 1)
    }

    //   发生 router 时 vmstack 会清空
    this._vmsStack = []

    let _vm = this._vms[p]

    let stack = {
      name: p,
      vm: _vm
    }

    if (stack.vm === undefined) {
      console.warn('routeTo() 加载 ' + p + ' vm 失败 --- event.js')
    }

    this._currentStack = stack

    this._vmsStack.push(stack)
  }

  // 根据路径 获取要操作窗口事件
  // 然后调用 对应窗口实例的 自定义属性 page.goforward()
  // paths = ['', '']
  gotoPath (paths) {
    this.routeTo(paths[0])
    let vm = this._currentStack.vm
    console.info(vm.$options['name'])
    if (!vm) {
      console.warn('gotoPath() _currentStack 为空 ---event.js')
      return
    }

    vm.$options['goforward'].call(vm, paths)
  }

  //  根据按钮 路径 来自动化操作
  gotoPathByBtn (btnpath) {
    let btns = btnpath.split('-')
    for (let index in btns) {
      let fnname = btns[index]
      this.click(fnname)
    }
  }

  /**/
  click (evt) {
    let keyname = evt.keyname
    let vm = this._currentStack.vm
    let path = this._currentStack.name

    if (vm === undefined) {
      console.warn('当前堆栈为空 --- click() --- event.js')
      return false
    }
    let name = vm.$options['name']

    let fn = vm.$options[keyname]

    if (fn === undefined) {
      console.warn('组件: ' + name + ' 未找到对应自定义函数[' + keyname + '] ---event.js')
      return false
    }
    this.clickMsg = null
    fn.call(vm, evt)

    this.callClickFn(path, keyname, this.clickMsg)
    this.clickMsg = null
    return true
  }

  getCurrentPath () {
    let path = ''
    for (let index in this._vmsStack) {
      path += (this._vmsStack[index].name + '/')
    }

    if (path.substr(-1) === '/') {
      let len = path.length
      path = path.substr(0, len - 1)
    }
    return path
  }
}



