// set页面
<template>
  <div id="ste">
    <div class="ste-div">SET</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      itemId: -1 // 定时器 定时跳转主页面
    }
  },
  methods: {
    // 定时器
    itemIdFn () {
      let _this = this
      this.clearTime()
      this.itemId = setTimeout(function () {
        _this.$frame.routeTo('/first')
      }, 2000)
    },
    // 清除定时器
    clearTime () {
      clearTimeout(this.itemId)
    }
  },
  created () {
    this.itemIdFn()
  }
}
</script>

<style>
#ste{
  width: 100%;
  height: 100%;
  text-align: center;
}
.ste-div{
  height: 39px;
  font-size: 20px;
  line-height: 39px;
}
</style>



