// 查看设置列表
<template>
  <div id="recall" v-router-page:page-name = "'/recall'">
  <div class="recall-div" v-for="(item,index) in this.$frame.gmdss.recall" :key="index"
    v-show= "indexs === index">{{item}}</div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  data () {
    return {
      gmdss: this.$frame.gmdss,
      indexs: 0,
      itemids: -1 // 定时获取时间
    }
  },
  watch: {
    // 循环选择
    indexs (ovel, nevl) {
      if (ovel < 0) {
        this.indexs = this.gmdss.recall.length - 1
      }
      if (ovel > this.gmdss.recall.length - 1) {
        this.indexs = 0
      }
    }
  },
  methods: {
    // 定时器设置时间
    itemId () {
      let hours = ''
      let mins = ''
      let _this = this
      this.clearTime()
      this.itemids = setInterval(function () {
        hours = _this.gmdss.time.getHours()
        mins = _this.gmdss.time.getMinutes()
        if (hours < 10) {
          hours = '0' + hours
        }
        if (mins < 10) {
          mins = '0' + mins
        }
        // Vue.set(_this.gmdss.recall, 0, 'TIME  ' + hours + ':' + mins)
        _this.gmdss.recall.splice(0, 1, 'TIME  ' + hours + ':' + mins)
        //  _this.gmdss.recall[0] = 'TIME  ' + hours + ':' + mins
      }, 1000)
    },
    // 清除定时器
    clearTime () {
      clearInterval(this.itemids)
    },
    // 向上选择
    recallUp () {
      this.indexs += 1
    },
    // 向下选择
    recallDown () {
      this.indexs -= 1
    },
    // 进入设置列表页面
    entFn () {
      if (this.indexs === 2 && this.gmdss.timeing.length > 0) {
        this.$frame.routeTo('/schedul')
      }
      if (this.indexs === 4) {
        this.$frame.routeTo('/first')
      }
    }
  },
  created () {
    this.itemId()
  },
  beforeRouteLeave (to, from, next) {
    this.clearTime()
    next()
  },
  [EventKeys.EVT_RCL] (evt) {
    this.$frame.routeTo('/program')
  },
  [EventKeys.EVT_UP] (evt) {
    console.log('up', '66666666666')
    this.recallUp()
  },
  [EventKeys.EVT_DOWN] (evt) {
    console.log('down', '66666666666')
    this.recallDown()
  },
  [EventKeys.EVT_ENT] (evt) {
    console.log('ent', '66666666666')
    this.entFn()
  },
  [EventKeys.EVT_POWER] (evt) {
  }
}
</script>

<style>
#recall{
  width: 100%;
  height: 100%;
  font-size: 23px;
  text-align: center;
  line-height: 39px;
}
</style>



