// 菜单选择
<template>
  <div id="modeMsg" v-router-page:page-name = "'/modeMsg'">
    <div class="modeMsg-div" v-for="(item,index) in this.$frame.gmdss.modeMsg" :key="index"
    v-show= "indexs === index">{{item}}</div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  data () {
    return {
      gmdss: this.$frame.gmdss,
      indexs: 0
    }
  },
  watch: {
    // 循环选择
    indexs (ovel, nvel) {
      if (ovel < 0) {
        this.indexs = this.gmdss.modeMsg.length - 1
      }
      if (ovel > this.gmdss.modeMsg.length - 1) {
        this.indexs = 0
      }
    }
  },
  methods: {
    // 向上选择
    modeUp () {
      this.indexs -= 1
    },
    // 向下选择
    modeDown () {
      this.indexs += 1
    },
    // ent选择
    modeEnt () {
      if (this.gmdss.print === false) {
        if (this.indexs === 0) {
          this.$frame.routeTo('/print')
        }
        if (this.indexs === 3) {
          this.$frame.routeTo('/first')
        }
      } else {
        // 停止打印
        if (this.indexs === 0) {
          this.gmdss.modeMsg.splice(this.indexs, 1, 'Manual START ？')
          this.gmdss.print = false
          this.$frame.routeTo('/first')
        }
      }
    }
  },
  [EventKeys.EVT_UP] (evt) {
    console.log('up', '66666666666')
    this.modeUp()
  },
  [EventKeys.EVT_DOWN] (evt) {
    console.log('down', '66666666666')
    this.modeDown()
  },
  [EventKeys.EVT_ENT] (evt) {
    console.log('ent', '66666666666')
    this.modeEnt()
  },
  [EventKeys.EVT_POWER] (evt) {
  }
}
</script>

<style>
#modeMsg{
  width: 100%;
  height: 100%;
  font-size: 23px;
  text-align: center;
  line-height: 39px;
}
</style>



