import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import itemled from '@/page/itemled'
import first from '@/page/first'
import modeMsg from '@/page/modeMsg'
import print from '@/page/print'
import recall from '@/page/recall'
import program from '@/page/program'
import timeing from '@/page/timeing'
import schedul from '@/page/schedul' // 设置列表
import erase from '@/page/erase' // 清空定时列表
import ste from '@/page/ste' // set页面
import ioc from '@/page/ioc' // IOC页面

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/itemled',
      name: 'itemled',
      component: itemled
    },
    {
      path: '/first',
      name: 'first',
      component: first
    },
    {
      path: '/modeMsg',
      name: 'modeMsg',
      component: modeMsg
    },
    {
      path: '/print',
      name: 'print',
      component: print
    },
    {
      path: '/recall',
      name: 'recall',
      component: recall
    },
    {
      path: '/program',
      name: 'program',
      component: program
    },
    {
      path: '/timeing',
      name: 'timeing',
      component: timeing
    },
    {
      path: '/schedul',
      name: 'schedul',
      component: schedul
    },
    {
      path: '/erase',
      name: 'erase',
      component: erase
    },
    {
      path: '/ste',
      name: 'ste',
      component: ste
    },
    {
      path: '/ioc',
      name: 'ioc',
      component: ioc
    }
  ]
})



