export class AppTimer {
  constructor (gmdss) {
    this.msec = 1000
    this.gmdss = gmdss
    this.intervalId = -1
  }

  start (msec = 1000) {
    this.msec = msec
    let that = this

    this.intervalId = setInterval(function () {
      that._doTimer()
    }, msec)
  }

  stop () {
    clearInterval(this.intervalId)
    this.intervalId = -1
  }

  _doTimer () {
    this.gmdss.doTimer()
  }
}



