<template>
  <div id="ioc-one" v-router-page:page-name = "'/ioc'">
    <div class="text">SPD/IOC:</div>
    <div class="ioc">
      <div>{{gmdss.ioc[iocindex][0]}}</div>
      <div :class= "index === 0 ? 'ioc-bottom' : undefined">{{gmdss.ioc[iocindex][1]}}</div>
      <div>{{gmdss.ioc[iocindex][2]}}</div>
      <div>/</div>
      <div>{{gmdss.scan[scanindex][0]}}</div>
      <div :class= "index === 1 ? 'ioc-bottom' : undefined">{{gmdss.scan[scanindex][1]}}</div>
      <div>{{gmdss.scan[scanindex][2]}}</div>
    </div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  data () {
    return {
      gmdss: this.$frame.gmdss,
      index: 0, // 光标位置
      iocindex: 0, // 扫描速度下标
      scanindex: 0 // 扫描密度下标
    }
  },
  methods: {
    // 光标左移
    printLeft () {
      if (this.index > 0) {
        this.index -= 1
      }
    },
    // 光标右移
    printRight () {
      if (this.index < 1) {
        this.index += 1
      }
    },
    // ioc向上
    printUp () {
      if (this.index === 0 && this.iocindex < this.gmdss.iocmin.length - 1) {
        this.iocindex += 1
      }
      if (this.index === 1 && this.scanindex < this.gmdss.scanmin.length - 1) {
        this.scanindex += 1
      }
    },
    // ioc 向下
    printDown () {
      if (this.index === 0 && this.iocindex > 0) {
        this.iocindex -= 1
      }
      if (this.index === 1 && this.scanindex > 0) {
        this.scanindex -= 1
      }
    }
  },
  [EventKeys.EVT_LEFT] (evt) {
    this.printLeft()
  },
  [EventKeys.EVT_RIGHT] (evt) {
    this.printRight()
  },
  [EventKeys.EVT_UP] (evt) {
    this.printUp()
    console.log('up', '66666666666')
  },
  [EventKeys.EVT_DOWN] (evt) {
    this.printDown()
    console.log('down', '66666666666')
  },
  [EventKeys.EVT_ENT] (evt) {
    this.$frame.routeTo('/first')
  },
  [EventKeys.EVT_POWER] (evt) {
  }
}
</script>

<style>
#ioc-one{
  display: flex;
  font-size: 22px;
  height: 39px;
  line-height: 39px;
}
.text{
  margin-left: 10%;
}
.ioc{
  display: flex;
  margin-left: 20%;
}
.ioc-bottom{
  border-bottom: 2px solid black;
}
</style>



