// 定时设置页面
<template>
  <div id="timeing" v-router-page:page-name = "'/timeing'">
  <div class="iteming-div"  v-show="!tiemType">
    <div :class= "index === 0 ? 'timeing-bom' : undefined">{{num[0]}}</div>
    <div :class= "index === 1 ? 'timeing-bom' : undefined">{{num[1]}}</div>
    <div :class= "index === 2 ? 'timeing-bom' : undefined">{{num[2]}}</div>
    </div>
  <div  v-show="!tiemType">{{numtext}}</div>
  <div>*</div>
  <div :class= "index === 3 ? 'timeing-bom' : undefined">{{item1hous}}</div>
  <div>:</div>
  <div class="iteming-div">
    <div :class= "index === 4 ? 'timeing-bom' : undefined">{{item1mins[0]}}</div>
    <div :class= "index === 5 ? 'timeing-bom' : undefined">{{item1mins[1]}}</div>
  </div>
  <div v-show="tiemType">-</div>
  <div v-show="tiemType" :class= "index === 6 ? 'timeing-bom' : undefined">{{item2hous}}</div>
  <div v-show="tiemType">:</div>
  <div v-show="tiemType" class="iteming-div">
    <div :class= "index === 7 ? 'timeing-bom' : undefined">{{item2mins[0]}}</div>
    <div :class= "index === 8 ? 'timeing-bom' : undefined">{{item2mins[1]}}</div>
  </div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  data () {
    return {
      gmdss: this.$frame.gmdss,
      tiemType: false, // false 显示左边 true 显示右边
      index: 0, // 光标下标
      num: '27*', // 通道
      numtext: 'NMC', // 通道名称
      item1hous: '9', // 时间一小时
      item1mins: '15', // 时间一分钟
      item2hous: '9', // 时间二小时
      item2mins: '30', // 时间二分钟
      list: ['*', '0'], // 保存星号后面的值
      indexs: 0
    }
  },
  watch: {
    // 监听下标显示时间设置
    index (ovel, nevl) {
      if (ovel > 5) {
        this.tiemType = true
      } else {
        this.tiemType = false
      }
    }
  },
  computed: {
    myCh () {
      return this.num
    }
  },
  methods: {
    // 根据评到检索数据
    chfns () {
      let _this = this
      let dataList = this.gmdss.maxList.filter(function (item) {
        return item.ch.substring(0, 2) === _this.myCh.substring(0, 2)
      })
      if (dataList.length > 0) {
        for (var i = 0; i < dataList.length; i++) {
          this.list.push(dataList[i].ch.substring(2, 3))
        }
      } else {
        this.list = ['*', '0']
      }
      console.log(this.list, '8888888888888888888')
    },
    // 光标左移
    timeingLeft () {
      if (this.index > 0) {
        this.index -= 1
      }
    },
    // 光标右移
    timeingRight () {
      if (this.index < 8) {
        this.index += 1
      }
    },
    // 修改值的方法
    insertStr (soure, start, newStr) {
      return soure.slice(0, start) + newStr + soure.slice(start)
    },
    // 通道值修改
    nums (num, min, max) {
      if (this.index < 3) {
        console.log('9999999999999999')
        if (this.index !== 2) {
          let chNum = parseInt(this.num[this.index])
          chNum += num
          if (chNum > max) {
            chNum = min
          }
          if (chNum < min) {
            chNum = max
          }
          this.list = ['*']
          this.num = this.num.slice(0, this.index) + this.num.slice(this.index + 1)
          this.num = this.insertStr(this.num, this.index, chNum + '')
          this.chfns()
        } else {
          this.indexs += num
          if (this.indexs > this.list.length - 1) {
            this.indexs = 0
          }
          if (this.indexs < 0) {
            this.indexs = this.list.length - 1
          }
          this.num = this.num.slice(0, this.index) + this.num.slice(this.index + 1)
          this.num = this.insertStr(this.num, this.index, this.list[this.indexs])
          console.log(this.list, '8888888888888')
        }
        // 修改通道名称
        let _this = this
        if (this.num[2] === '*') {
          let dataList = this.gmdss.maxList.filter(function (item) {
            return item.ch === _this.myCh.substring(0, 2) + '0'
          })
          if (dataList.length > 0) {
            this.numtext = dataList[0].chText
          }
          console.log(dataList, '666666666666666')
        } else {
          let dataList1 = this.gmdss.maxList.filter(function (item) {
            return item.ch === _this.myCh
          })
          if (dataList1.length > 0) {
            this.numtext = dataList1[0].chText
          }
        }
      }
    },
    // 时间小时 修改
    iteme (num, min, max) {
      // 第一时间小时修改
      if (this.index === 3) {
        let iteme1H = parseInt(this.item1hous)
        iteme1H += num
        if (iteme1H > max) {
          iteme1H = min
        }
        if (iteme1H < min) {
          iteme1H = max
        }
        this.item1hous = iteme1H + ''
      }
      // 第二时间小时修改
      if (this.index === 6) {
        let iteme2H = parseInt(this.item2hous)
        iteme2H += num
        if (iteme2H > max) {
          iteme2H = min
        }
        if (iteme2H < min) {
          iteme2H = max
        }
        this.item2hous = iteme2H + ''
      }
    },
    // 分钟 修改
    mins (num, min, max1, max2) {
      // 第一时间分钟修改
      if (this.index === 4 || this.index === 5) {
        if (this.index === 4) {
          let mins1 = parseInt(this.item1mins[0])
          mins1 += num
          if (mins1 > max1) {
            mins1 = min
          }
          if (mins1 < min) {
            mins1 = max1
          }
          this.item1mins = this.item1mins.slice(0, 0) + this.item1mins.slice(0 + 1)
          this.item1mins = this.insertStr(this.item1mins, 0, mins1 + '')
        }
        if (this.index === 5) {
          let mins2 = parseInt(this.item1mins[1])
          mins2 += num
          if (mins2 > max2) {
            mins2 = min
          }
          if (mins2 < min) {
            mins2 = max2
          }
          this.item1mins = this.item1mins.slice(0, 1) + this.item1mins.slice(1 + 1)
          this.item1mins = this.insertStr(this.item1mins, 1, mins2 + '')
        }
      }
      // 第二时间分钟修改
      if (this.index === 7 || this.index === 8) {
        if (this.index === 7) {
          let mins3 = parseInt(this.item2mins[0])
          mins3 += num
          if (mins3 > max1) {
            mins3 = min
          }
          if (mins3 < min) {
            mins3 = max1
          }
          this.item2mins = this.item2mins.slice(0, 0) + this.item2mins.slice(0 + 1)
          this.item2mins = this.insertStr(this.item2mins, 0, mins3 + '')
        }
        if (this.index === 8) {
          let mins4 = parseInt(this.item2mins[1])
          mins4 += num
          if (mins4 > max2) {
            mins4 = min
          }
          if (mins4 < min) {
            mins4 = max2
          }
          this.item2mins = this.item2mins.slice(0, 1) + this.item2mins.slice(1 + 1)
          this.item2mins = this.insertStr(this.item2mins, 1, mins4 + '')
        }
      }
    },
    // 修改后的时间保存在定时列表中
    entFn () {
      let obj = {
        ch: this.num,
        chText: this.numtext,
        item1hous: this.item1hous,
        item1mins: this.item1mins,
        item2hous: this.item2hous,
        item2mins: this.item2mins
      }
      let str = this.num + '  ' + this.numtext + '  '
      str += this.item1hous + ':' + this.item1mins + '-'
      str += this.item2hous + ':' + this.item2mins
      this.gmdss.timeing.push(obj)
      this.gmdss.strtimeing.push(str)
      this.gmdss.strpaper = -18 * this.gmdss.strtimeing.length
      this.gmdss.strprint = false
      this.$frame.routeTo('/ste')
      console.log(this.gmdss.timeing, '11111111111111111111')
    }
  },
  [EventKeys.EVT_LED] (evt) {
  },
  [EventKeys.EVT_RCL] (evt) {
  },
  [EventKeys.EVT_MOPE] (evt) {
  },
  [EventKeys.EVT_LEFT] (evt) {
    this.timeingLeft()
  },
  [EventKeys.EVT_RIGHT] (evt) {
    this.timeingRight()
  },
  [EventKeys.EVT_UP] (evt) {
    this.nums(1, 0, 9)
    this.iteme(1, 0, 23)
    this.mins(1, 0, 5, 9)
  },
  [EventKeys.EVT_DOWN] (evt) {
    this.nums(-1, 0, 9)
    this.iteme(-1, 0, 23)
    this.mins(-1, 0, 5, 9)
  },
  [EventKeys.EVT_TIM] (evt) {
  },
  [EventKeys.EVT_PHASE] (evt) {
  },
  [EventKeys.EVT_IOC] (evt) {
  },
  [EventKeys.EVT_PH] (evt) {
  },
  [EventKeys.EVT_ENT] (evt) {
    this.entFn()
  },
  [EventKeys.EVT_POWER] (evt) {
  }
}
</script>

<style>
#timeing {
  width: 100%;
  height: 100%;
  font-size: 23px;
  text-align: center;
  display: flex;
  justify-content: space-around;
}
#timeing > div {
height: 35px;
line-height: 35px;
}
.timeing-bom{
  border-bottom: 2px solid black;
}
.iteming-div{
  display: flex;
}
</style>



