<template>
  <div id="itemled">
    <div class="itemled-left">TIME</div>
    <div class="itemled-right">{{item}}</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      gmdss: this.$frame.gmdss
    }
  },
  methods: {
    rouTo () {
      let _this = this
      this.gmdss.itemid = setTimeout(function () {
        _this.$frame.routeTo('/first')
      }, 3000)
    }
  },
  computed: {
    item () {
      let data = new Date()
      let hous = data.getHours()
      let mise = data.getMinutes()
      if (hous < 10) {
        hous = '0' + hous
      }
      if (mise < 10) {
        mise = '0' + mise
      }
      return hous + ':' + mise
    }
  },
  created () {
    this.rouTo()
  }
}
</script>

<style>
#itemled {
  display: flex;
  width: 100%;
  height: 100%;
}
.itemled-left{
  height: 39px;
  margin-left: 20%;
  font-size: 20px;
  line-height: 39px;
}
.itemled-right{
  height: 39px;
  line-height: 39px;
  margin-left: 20%;
  font-size: 20px;
}
</style>



