<template>
  <div id='first' v-router-page:page-name = "'/first'">
    <div class="first-One">
      <div :class= "chindex === 0 ? 'btn-bg' : undefined">{{ch[0]}}</div>
      <div :class= "chindex === 1 ? 'btn-bg' : undefined">{{ch[1]}}</div>
      <div :class= "chindex === 2 ? 'btn-bg' : undefined">{{ch[2]}}</div>
      <div v-show="!star">N</div>
      <div></div>
      <div>{{chText[0]}}</div>
      <div>{{chText[1]}}</div>
      <div>{{chText[2]}}</div>
      <div></div>
      <div></div>
      <div v-show="!star" :class= "index === 0 ? 'btn-bottom' : undefined">{{req[0]}}</div>
      <div v-show="!star" :class= "index === 1 ? 'btn-bottom' : undefined">{{req[1]}}</div>
      <div v-show="!star" :class= "index === 2 ? 'btn-bottom' : undefined">{{req[2]}}</div>
      <div v-show="!star" :class= "index === 3 ? 'btn-bottom' : undefined">{{req[3]}}</div>
      <div v-show="!star" :class= "index === 4 ? 'btn-bottom' : undefined">{{req[4]}}</div>
      <div v-show="!star">.</div>
      <div v-show="!star" :class= "index === 5 ? 'btn-bottom' : undefined">{{req[5]}}</div>
      <div v-show="star">Scan</div>
    </div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  data () {
    return {
      gmdss: this.$frame.gmdss,
      index: 0, // 频率光标位置
      chindex: -1, // CH光标位置
      // req: ['2', '2', '7', '7', '0', '0'], // 频率
      req: '135790',
      chType: false, // false 设置频率 true设置CH
      ch: '10*', // CH值
      chText: 'JMH', // 通道名称
      list: ['*', '0'], // 保存星号后面的值
      indexs: 0, // 星号数组初始下标
      star: false
    }
  },
  watch: {
  },
  computed: {
    myCh () {
      return this.ch
    }
  },
  methods: {
    // 根据评到检索数据
    chfns () {
      let _this = this
      let dataList = this.gmdss.maxList.filter(function (item) {
        return item.ch.substring(0, 2) === _this.myCh.substring(0, 2)
      })
      if (dataList.length > 0) {
        for (var i = 0; i < dataList.length; i++) {
          this.list.push(dataList[i].ch.substring(2, 3))
        }
      } else {
        this.list = ['*', '0']
      }
      console.log(this.list, '8888888888888888888')
    },
    // 根据频率查找映射表
    reqFn () {
      let _this = this
      let dataList = this.gmdss.maxList.filter(function (item) {
        return item.req === _this.req
      })
      if (dataList.length > 0) {
        this.ch = dataList[0].ch
        this.chText = dataList[0].chText
      }
    },
    // ch值设置
    chUp (num, min, max) {
      if (this.chindex !== 2) {
        let chNum = parseInt(this.ch[this.chindex])
        chNum += num
        if (chNum > max) {
          chNum = min
        }
        if (chNum < min) {
          chNum = max
        }
        this.list = ['*']
        this.ch = this.ch.slice(0, this.chindex) + this.ch.slice(this.chindex + 1)
        this.ch = this.insertStr(this.ch, this.chindex, chNum + '')
        this.chfns()
      } else {
        this.indexs += num
        if (this.indexs > this.list.length - 1) {
          this.indexs = 0
        }
        if (this.indexs < 0) {
          this.indexs = this.list.length - 1
        }
        this.ch = this.ch.slice(0, this.chindex) + this.ch.slice(this.chindex + 1)
        this.ch = this.insertStr(this.ch, this.chindex, this.list[this.indexs])
        console.log(this.list, '8888888888888')
      }
    },
    // 修改值的方法
    insertStr (soure, start, newStr) {
      return soure.slice(0, start) + newStr + soure.slice(start)
    },
    // 修改值向上
    requp (num, min, max) {
      // 修改频率
      if (this.chType === false) {
        let reqNum = parseInt(this.req[this.index])
        reqNum += num
        if (reqNum > max) {
          reqNum = min
        }
        if (reqNum < min) {
          reqNum = max
        }
        // let reqNums = reqNum + ''
        // this.req[this.index] = reqNum + ''
        // this.req.splice(this.index, 1, this.req[this.index])
        this.req = this.req.slice(0, this.index) + this.req.slice(this.index + 1)
        this.req = this.insertStr(this.req, this.index, reqNum + '')
        this.reqFn()
        // console.log(reqNum, this.req[this.index], this.req, typeof (this.index), '55555555555555')
      } else {
        this.chUp(num, min, max)
      }
    },
    // 光标左移
    left () {
      if (this.index > 0 && this.chType === false) {
        this.index -= 1
      }
      if (this.chindex > 0 && this.chType === true) {
        this.chindex -= 1
      }
    },
    // 光标右移
    right () {
      if (this.index < 5 && this.chType === false) {
        this.index += 1
      }
      if (this.chindex < 2 && this.chType === true) {
        this.chindex += 1
      }
    },
    // ch值设置
    chFn () {
      this.chType = true
      this.chindex = 0
    },
    // ent 保存数据
    entFn () {
      this.index = 0
      this.indexs = 0
      this.chindex = -1
      let _this = this
      this.list = ['*', '0']
      if (this.ch[2] === '*') {
        this.star = true
        let dataList = this.gmdss.maxList.filter(function (item) {
          return item.ch === _this.myCh.substring(0, 2) + '0'
        })
        if (dataList.length > 0) {
          this.chText = dataList[0].chText
          this.req = dataList[0].req
        }
        setTimeout(function () {
          _this.star = false
        }, 2000)
      } else {
        let dataList1 = this.gmdss.maxList.filter(function (item) {
          return item.ch === _this.myCh
        })
        if (dataList1.length > 0) {
          this.chText = dataList1[0].chText
          this.req = dataList1[0].req
          this.chType = false
        }
      }
      this.gmdss.req = this.req
    }
  },
  [EventKeys.EVT_LED] (evt) {
  },
  [EventKeys.EVT_RCL] (evt) {
    this.$frame.routeTo('/recall')
  },
  [EventKeys.EVT_MOPE] (evt) {
    this.$frame.routeTo('/modeMsg')
  },
  [EventKeys.EVT_LEFT] (evt) {
    this.left()
  },
  [EventKeys.EVT_RIGHT] (evt) {
    this.right()
  },
  [EventKeys.EVT_UP] (evt) {
    this.requp(1, 0, 9)
  },
  [EventKeys.EVT_DOWN] (evt) {
    this.requp(-1, 0, 9)
  },
  [EventKeys.EVT_TIM] (evt) {
    console.log('tim', '66666666666')
  },
  [EventKeys.EVT_PHASE] (evt) {
    console.log('phase', '66666666666')
  },
  [EventKeys.EVT_IOC] (evt) {
    console.log('ioc', '66666666666')
  },
  [EventKeys.EVT_PH] (evt) {
    this.chFn()
    console.log('ph', '66666666666')
  },
  [EventKeys.EVT_ENT] (evt) {
    this.entFn()
    console.log('ent', '66666666666')
  },
  [EventKeys.EVT_POWER] (evt) {
  }
}
</script>

<style>
#first{
  width: 100%;
  height: 100%;
  position: relative;
}
.first-One > div{
  width: 16px;
  height: 36px;
  text-align: center;
}
.first-One{
  display: flex;
  font-size: 20px;
  height: 36px;
  line-height: 36px;
}
.first-div{
  width: 3px;
  height: 3px;
  border-radius: 50%;
  position: absolute;
  left: 84.5%;
  top: 18px;
  background-color: black;
}
/* 光标*/
.btn-bottom{
  border-bottom: 1px solid black;
}
.btn-bg{
  background-color: black;
  color: white;
}
</style>



