// 设置列表
<template>
  <div id="schedul" v-router-page:page-name = "'/schedul'">
  <div class="schedul-div"  v-show="!tiemType">
    <div :class= "index === 0 ? 'schedul-bom' : undefined">{{objet.ch[0]}}</div>
    <div :class= "index === 1 ? 'schedul-bom' : undefined">{{objet.ch[1]}}</div>
    <div :class= "index === 2 ? 'schedul-bom' : undefined">{{objet.ch[2]}}</div>
    </div>
  <div  v-show="!tiemType">{{objet.chText}}</div>
  <div>*</div>
  <div :class= "index === 3 ? 'schedul-bom' : undefined">{{objet.item1hous}}</div>
  <div>:</div>
  <div class="schedul-div">
    <div :class= "index === 4 ? 'schedul-bom' : undefined">{{objet.item1mins[0]}}</div>
    <div :class= "index === 5 ? 'schedul-bom' : undefined">{{objet.item1mins[1]}}</div>
  </div>
  <div v-show="tiemType">-</div>
  <div v-show="tiemType" :class= "index === 6 ? 'schedul-bom' : undefined">{{objet.item2hous}}</div>
  <div v-show="tiemType">:</div>
  <div v-show="tiemType" class="schedul-div">
    <div :class= "index === 7 ? 'schedul-bom' : undefined">{{objet.item2mins[0]}}</div>
    <div :class= "index === 8 ? 'schedul-bom' : undefined">{{objet.item2mins[1]}}</div>
  </div>
  </div>
</template>

<script>
import * as EventKeys from '@/utils/event-key'
export default {
  data () {
    return {
      gmdss: this.$frame.gmdss,
      index: -1, // 光标下标
      indexs: 0, // 数组下标
      tiemType: false,
      list: ['*'],
      listindex: 0,
      rclPrg: false // 是否允许设置时间
    }
  },
  watch: {
    index (ovel, nevl) {
      if (ovel > 5) {
        this.tiemType = true
      } else {
        this.tiemType = false
      }
    }
  },
  computed: {
    objet () {
      let list = this.gmdss.timeing
      let objest = null
      if (this.gmdss.timeing.length > 0) {
        objest = list[this.indexs]
      }
      return objest
    }
  },
  methods: {
    // 根据评到检索数据
    chfns () {
      let _this = this
      let dataList = this.gmdss.maxList.filter(function (item) {
        return item.ch.substring(0, 2) === _this.objet.ch.substring(0, 2)
      })
      if (dataList.length > 0) {
        for (var i = 0; i < dataList.length; i++) {
          this.list.push(dataList[i].ch.substring(2, 3))
        }
      } else {
        this.list = ['*', '0']
      }
      console.log(this.list, '8888888888888888888')
    },
    // 修改值的方法
    insertStr (soure, start, newStr) {
      return soure.slice(0, start) + newStr + soure.slice(start)
    },
    // 上下显示切换
    showFn (num) {
      if (this.index === -1) {
        this.indexs += num
        if (this.indexs > this.gmdss.timeing.length - 1) {
          this.indexs = 0
        }
        if (this.indexs < 0) {
          this.indexs = this.gmdss.timeing.length - 1
        }
      }
    },
    // 通道值修改
    nums (num, min, max) {
      if (this.index < 3) {
        if (this.index !== 2) {
          let chNum = parseInt(this.objet.ch[this.index])
          chNum += num
          if (chNum > max) {
            chNum = min
          }
          if (chNum < min) {
            chNum = max
          }
          this.list = ['*']
          this.objet.ch = this.objet.ch.slice(0, this.index) + this.objet.ch.slice(this.index + 1)
          this.objet.ch = this.insertStr(this.objet.ch, this.index, chNum + '')
          this.chfns()
        } else {
          this.listindex += num
          if (this.listindex > this.list.length - 1) {
            this.listindex = 0
          }
          if (this.listindex < 0) {
            this.listindex = this.list.length - 1
          }
          this.objet.ch = this.objet.ch.slice(0, this.index) + this.objet.ch.slice(this.index + 1)
          this.objet.ch = this.insertStr(this.objet.ch, this.index, this.list[this.listindex])
        }
        // 修改通道名称
        let _this = this
        if (this.objet.ch[2] === '*') {
          let dataList = this.gmdss.maxList.filter(function (item) {
            return item.ch === _this.objet.ch.substring(0, 2) + '0'
          })
          if (dataList.length > 0) {
            this.objet.chText = dataList[0].chText
          }
        } else {
          let dataList1 = this.gmdss.maxList.filter(function (item) {
            return item.ch === _this.objet.ch
          })
          if (dataList1.length > 0) {
            this.objet.chText = dataList1[0].chText
          }
        }
      }
    },
    // 时间小时修改
    hourFn (num, min, hourmax) {
      // 第一时间小时修改
      if (this.index === 3) {
        let iteme1H = parseInt(this.objet.item1hous)
        iteme1H += num
        if (iteme1H > hourmax) {
          iteme1H = min
        }
        if (iteme1H < min) {
          iteme1H = hourmax
        }
        this.objet.item1hous = iteme1H + ''
      }
      // 第二时间小时修改
      if (this.index === 6) {
        let iteme2H = parseInt(this.objet.item2hous)
        iteme2H += num
        if (iteme2H > hourmax) {
          iteme2H = min
        }
        if (iteme2H < min) {
          iteme2H = hourmax
        }
        this.objet.item2hous = iteme2H + ''
      }
    },
    // 分钟 修改
    minsFn (num, min, minmax1, minmax2) {
      // 第一时间分钟修改
      if (this.index === 4 || this.index === 5) {
        if (this.index === 4) {
          let mins1 = parseInt(this.objet.item1mins[0])
          mins1 += num
          if (mins1 > minmax1) {
            mins1 = min
          }
          if (mins1 < min) {
            mins1 = minmax1
          }
          this.objet.item1mins = this.objet.item1mins.slice(0, 0) + this.objet.item1mins.slice(0 + 1)
          this.objet.item1mins = this.insertStr(this.objet.item1mins, 0, mins1 + '')
        }
        if (this.index === 5) {
          let mins2 = parseInt(this.objet.item1mins[1])
          mins2 += num
          if (mins2 > minmax2) {
            mins2 = min
          }
          if (mins2 < min) {
            mins2 = minmax2
          }
          this.objet.item1mins = this.objet.item1mins.slice(0, 1) + this.objet.item1mins.slice(1 + 1)
          this.objet.item1mins = this.insertStr(this.objet.item1mins, 1, mins2 + '')
        }
      }
      // 第二时间分钟修改
      if (this.index === 7 || this.index === 8) {
        if (this.index === 7) {
          let mins3 = parseInt(this.objet.item2mins[0])
          mins3 += num
          if (mins3 > minmax1) {
            mins3 = min
          }
          if (mins3 < min) {
            mins3 = minmax1
          }
          this.objet.item2mins = this.objet.item2mins.slice(0, 0) + this.objet.item2mins.slice(0 + 1)
          this.objet.item2mins = this.insertStr(this.objet.item2mins, 0, mins3 + '')
        }
        if (this.index === 8) {
          let mins4 = parseInt(this.objet.item2mins[1])
          mins4 += num
          if (mins4 > minmax2) {
            mins4 = min
          }
          if (mins4 < min) {
            mins4 = minmax2
          }
          this.objet.item2mins = this.objet.item2mins.slice(0, 1) + this.objet.item2mins.slice(1 + 1)
          this.objet.item2mins = this.insertStr(this.objet.item2mins, 1, mins4 + '')
        }
      }
    },
    // 显示切换
    schedFn (num, min, max, hourmax, minmax1, minmax2) {
      console.log(this.gmdss.timeing)
      switch (this.index) {
        case -1:
          this.showFn(num)
          break
        case 0:
          if (this.rclPrg === true) {
            this.nums(num, min, max)
          }
          console.log(this.objet.ch[0], '555555555555')
          break
        case 1:
          if (this.rclPrg === true) {
            this.nums(num, min, max)
          }
          break
        case 2:
          if (this.rclPrg === true) {
            this.nums(num, min, max)
          }
          break
        case 3:
          if (this.rclPrg === true) {
            this.hourFn(num, min, hourmax)
          }
          break
        case 4:
          if (this.rclPrg === true) {
            this.minsFn(num, min, minmax1, minmax2)
          }
          break
        case 5:
          if (this.rclPrg === true) {
            this.minsFn(num, min, minmax1, minmax2)
          }
          break
        case 6:
          if (this.rclPrg === true) {
            this.hourFn(num, min, hourmax)
          }
          break
        case 7:
          if (this.rclPrg === true) {
            this.minsFn(num, min, minmax1, minmax2)
          }
          break
        case 8:
          if (this.rclPrg === true) {
            this.minsFn(num, min, minmax1, minmax2)
          }
          break
      }
    },
    // 光标切换
    indexFn (num, min, max) {
      this.index += num
      if (this.index < min) {
        this.index = max
      }
      if (this.index > max) {
        this.index = min
      }
    }
  },
  [EventKeys.EVT_LED] (evt) {
  },
  [EventKeys.EVT_RCL] (evt) {
    this.rclPrg = true
  },
  [EventKeys.EVT_MOPE] (evt) {
  },
  [EventKeys.EVT_LEFT] (evt) {
    this.indexFn(-1, 0, 8)
  },
  [EventKeys.EVT_RIGHT] (evt) {
    this.indexFn(1, 0, 8)
  },
  [EventKeys.EVT_UP] (evt) {
    this.schedFn(1, 0, 9, 23, 5, 9)
  },
  [EventKeys.EVT_DOWN] (evt) {
    this.schedFn(-1, 0, 9, 23, 5, 9)
  },
  [EventKeys.EVT_TIM] (evt) {
  },
  [EventKeys.EVT_PHASE] (evt) {
  },
  [EventKeys.EVT_IOC] (evt) {
  },
  [EventKeys.EVT_PH] (evt) {
  },
  [EventKeys.EVT_ENT] (evt) {
    if (this.rclPrg === true) {
      this.rclPrg = false
      this.$frame.routeTo('/ste')
    }
  },
  [EventKeys.EVT_POWER] (evt) {
  }
}
</script>

<style>
#schedul{
  width: 100%;
  height: 100%;
  font-size: 23px;
  text-align: center;
  display: flex;
  justify-content: space-around;
}
#schedul > div {
height: 35px;
line-height: 35px;
}
.schedul-bom{
  border-bottom: 2px solid black;
}
.schedul-div{
  display: flex;
}
</style>



