import {VueEvent} from './Event'
import {VueGMDSS} from '@/gmdss'
import router from '@/router'
import {AppTimer} from './AppTimer'

export class VueFrame {
  constructor () {
    this.name = 'VueFrame'

    //  router 在 main.js 里赋值
    console.info('frame')

    this.router = router

    this.event = new VueEvent()
    this.gmdss = new VueGMDSS()

    this.timer = new AppTimer(this.gmdss)
    this.timer.start(1000)
  }

  addVm (pagename, vm) {
    this.event.addVm(pagename, vm)
  }

  removeVm (pagename) {
    this.event.removeVm(pagename)
  }

  pushStack (pagename, vm) {
    this.event.pushStack(pagename, vm)
  }

  popStack () {
    this.event.popStack()
  }

  getCurrentVm () {
    return this.event.getCurrentVm()
  }

  //  page = ''   page 页面 地址
  //  params = {} 预留参数
  routeTo (page, params) {
    this.router.push(page)
    let that = this
    return new Promise(function () {
      that.router.app.$nextTick(function () {
        let _vm = that.event.getVm(page)
        if (_vm === undefined) {
          console.warn('未找到对应 route Page: ' + page + '---frame.js')
        }
        // let fn
        // fn = _vm.$options['beforePageRouteEnter']
        // if (fn !== undefined) {
        //   fn.call(_vm)
        // } else {
        //   console.info('page: ' + page + ' 未找到函数 beforePageRouteEnter() --- frame.js')
        // }

        that.event.routeTo(page, params)

        // fn = _vm.$options['beforePageRouteLeave']
        // if (fn !== undefined) {
        //   fn.call(_vm)
        // } else {
        //   console.info('page: ' + page + ' 未找到函数 beforePageRouteLeave() --- frame.js')
        // }

        console.info('route to: ' + page)
      })
    })
  }

  // 根据指定路径 来打开对应窗口
  // eg. /menu/display/setting
  gotoPath (path) {
    if (path[0] === '/') {
      let len = path.length
      path = path.substr(1, len - 1)
    }

    let paths = path.split('/')

    this.router.push(paths[0])

    let that = this
    this.router.app.$nextTick(function () {
      that.event.gotoPath(paths)
    })

    console.info('route to path: ' + path)
  }

  //  窗口根据按钮操作顺序来显示
  //  pagename = ''  从哪个 route 开始的page eg. '/menu'
  //  btnpath = ''
  //  btnpath eg. 'down-down-down-enter-down-down-enter'
  gotoPathByBtn (pagename, btnpath) {
    this.router.push(pagename)

    let that = this
    this.router.app.$nextTick(function () {
      that.event.routeTo(pagename)
      that.event.gotoPathByBtn(btnpath)
    })
  }

  click (evt) {
    return this.event.click(evt)
  }

  getCurrentPath () {
    return this.event.getCurrentPath()
  }
}



